.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 96px;
  margin: auto;
}

.card {
  width: 110%;
  padding: 25px 35px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  background-color: rgba(128, 128, 128, 0.2);
  backdrop-filter: blur(7px);
}

.title {
  font-size: 1.875rem;
  font-weight: 600;
  text-align: center;
  padding-bottom: 15px;
  color: #d1d5db;
}

.highlight {
  color: #7c2d12;
}

.label {
  font-size: 1rem; 
  color: #94a3b8; 
}

.input {
  width: 100%;
  border: 1px solid #d1d5db;
  height: 40px;
  padding: 8px;
  margin: 10px 0;
  border-radius: 4px;
}

.link {
  font-size: 0.875rem;
  color: #94a3b8; 
  margin-top: 8px;
  display: inline-block;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
  color: #8d4027;
}

.button {
  width: 100%;
  padding: 8px;
  border-radius: 4px;
  background-color: rgb(230, 230, 230);
  color: black;
  font-size: 0.875rem;
  margin-top: 8px;
  text-align: center;
}

.button:hover {
  background-color: rgb(158, 155, 155);
}

.button:disabled {
  background-color: #d1d5db;
  cursor: not-allowed;
}

.spinner {
  display: inline-block;
  border: 2px solid #c2bfbf; 
  border-top: 2px solid rgb(118, 115, 115);
  border-radius: 50%;
  width: 12px;
  height: 12px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
